import React from "react";
import * as chromatic from 'd3-scale-chromatic';
import * as d3 from 'd3';

type colorChoice = 'RdYlBu'|'RdYlGn'|'Spectral'|'Turbo'|'Cool'|'Warm'|'Plasma'|'Viridis'|'Inferno'|'Greys'
export const invertType={
    'RdYlBu':true,
    'RdYlGn':true,
    'Spectral':true,
    'Greys':true
}

interface proprs extends React.SVGAttributes<SVGGElement>{
    // colorType: Function,
    value:colorChoice,
    direction?:'vertical'|'horizontal',
    invert?: boolean
    domain?: number[],
}
export const Legend: React.FC<proprs> = (props) => {
    // return <>{value.map((c,i)=><div style={{display:'inline-block'}}><ColorPicker key={i} color={c} onChange={(val) => {value[i]=val; onChange(value)}} /></div>)}</>;
    const stringCh = `interpolate${props.value}`;

    const width = props.width??400;
    const height = props.height??10;
    const direction = props.direction??'vertical';
    const domain = props.domain??[0,1];
    // @ts-ignore
    const colorType:Function = chromatic[stringCh];

    function ramp_verti(color:Function, n = 256) {
        const canvas = createContext(1, n);
        const context = canvas.getContext("2d");
        // @ts-ignore
        const invert = props.invert??invertType[stringCh];
        const scale = d3.scaleLinear().domain([0,n]).range(domain);
        if(context){
            for (let i = 0; i < n; ++i) {
                // if(invert)
                //     context.fillStyle = color((n-i-1) / (n - 1));
                // else
                //     context.fillStyle = color(i / (n - 1));
                context.fillStyle = color(scale(i));
                context.fillRect(0, i, 1, 1);
            }
        }
        return canvas;
    }
    function ramp_hori(color:Function, n = 256) {
        const canvas = createContext(n, 1);
        const context = canvas.getContext("2d");
        // @ts-ignore
        const invert = props.invert??invertType[stringCh];
        if(context){
            for (let i = 0; i < n; ++i) {
                if(invert)
                    context.fillStyle = color((n-i-1) / (n - 1));
                else
                    context.fillStyle = color(i / (n - 1));
                context.fillRect(i, 0, 1, 1);
            }
        }
        return canvas;
    }
    const ramp = direction==='vertical'?ramp_verti:ramp_hori;
    function createContext(width:number, height:number) {
        var canvas = document.createElement('canvas');
        canvas.width = width;
        canvas.height = height;
        return canvas;
    }

    // return <svg viewBox={`0 0 ${width} ${height}`}>
    return    <image preserveAspectRatio={'none'} href={ramp(colorType).toDataURL()} width={width} height={height}/>
    // </svg>
};
