import {GrafanaTheme2} from "@grafana/data";
import React from "react";
import {stylesFactory} from "@grafana/ui";
import {css} from "@emotion/css";
import * as d3 from "d3";
import { multiFormat } from 'Ulti';
import {ScaleOrdinal} from "d3";
import {Legend} from "./legend";

interface Props {
    width: number,
    height: number,
    color?: ScaleOrdinal<any,any>,
    data: LineInput[],
    theme: GrafanaTheme2,
    mark?:Date,
    xRange?:[number|Date,number|Date],
    yRange?:[number,number]
}
export interface LineInput {
    key: string,
    value: [number|Date,number|undefined][],
    color?:string
}
interface States {
    zoomTransform: {x:number,y:number,k:number,rescaleX:Function,rescaleY:Function},
    margin: { top: number, bottom: number, left: number, right: number },
    data: LineInput[],
}

export default class LineChart extends React.Component<Props, States> {
    svgRef = React.createRef<SVGSVGElement>();
    widthView() {
        return this.props.width
    }

    heightView() {
        return this.props.height
    }

    widthG() {
        return this.widthView() - (this.state.margin ?? {
            top: 0,
            bottom: 0,
            left: 0,
            right: 0
        }).left - (this.state.margin ?? {top: 0, bottom: 0, left: 0, right: 0}).right
    }

    heightG() {
        return this.heightView() - (this.state.margin ?? {
            top: 0,
            bottom: 0,
            left: 0,
            right: 0
        }).top - (this.state.margin ?? {top: 0, bottom: 0, left: 0, right: 0}).bottom
    }
    svg = d3.select<SVGSVGElement|null,unknown>(null)
    g = d3.select<SVGGElement|null,unknown>(null)
    zoom = d3.zoom<SVGRectElement, unknown>()
        .scaleExtent([-5, 5])
        .on("zoom", this.zoomed.bind(this));
    zoomed() {
        this.setState({
            zoomTransform: d3.event.transform,
            // fontSize: this.state.opts.fontSize/event.transform.k
        });
    }
    y = d3.scaleLinear()
    x = d3.scaleTime();
    LineFunc =  d3.line<[Date|number,number|undefined]>()
        // .interpolate("linear-closed")
    .curve(d3.curveMonotoneX)
    .x((d) => {
        return this.x(d[0]) as number;
    })
    .y((d) => {
        return this.y(d[1] as number) as number;
    }).defined((d)=>{
        return (d && d && (d[1] !== undefined));
        })

    constructor(props:Props) {
        super(props);
        this.state ={
            zoomTransform: {x:0,y:0,k:1,rescaleX:(d:any)=>d,rescaleY:(d:any)=>d},
            margin: { top: 10, bottom: 40, left: 40, right: 10 },
            data: [],
        }
    }
    componentDidMount(): void {
        // const {margin} = this.state;
        this.svg = d3.select<SVGSVGElement|null,unknown>(this.svgRef.current);
        this.svg.select<SVGRectElement>('rect.pantarget').call(this.zoom.bind(this))//.call(this.zoom.transform, d3.zoomIdentity.translate(margin.left, margin.top));;
        if(this.props.xRange){
            this.x.domain(this.props.xRange);
        }else {
            const xRange = [d3.min(this.props.data, d => d3.min(d.value, e => e ? e[0] : undefined)) as Date, d3.max(this.props.data, d => d3.max(d.value, e => e ? e[0] : undefined)) as Date];
            if ((xRange[0] !== undefined) && (xRange[1] !== undefined))
                this.x.domain(xRange);
        }
        if(this.props.yRange){
            this.y.domain(this.props.yRange);
        }else{
            const yRange = [d3.min(this.props.data,d=>d3.min(d.value,e=>e?e[1]:undefined)) as number,d3.max(this.props.data,d=>d3.max(d.value,e=>e?e[1]:undefined)) as number];
            if(yRange[0]!==undefined && yRange[1]!==undefined)
                this.y.domain(yRange);
        }
        this.setState({data:this.props.data})
    }
    componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<States>, snapshot?: any): void {
        if(this.props.data!==prevProps.data){
            if(this.props.xRange){
                this.x.domain(this.props.xRange);
            }else {
                const xRange = [d3.min(this.props.data, d => d3.min(d.value, e => e ? e[0] : undefined)) as Date, d3.max(this.props.data, d => d3.max(d.value, e => e ? e[0] : undefined)) as Date];
                if ((xRange[0] !== undefined) && (xRange[1] !== undefined))
                    this.x.domain(xRange);
            }
            if(this.props.yRange){
                this.y.domain(this.props.yRange);
            }else{
                const yRange = [d3.min(this.props.data,d=>d3.min(d.value,e=>e?e[1]:undefined)) as number,d3.max(this.props.data,d=>d3.max(d.value,e=>e?e[1]:undefined)) as number];
                if(yRange[0]!==undefined && yRange[1]!==undefined)
                    this.y.domain(yRange);
            }
            this.setState({data:this.props.data})
        }
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const styles = getStyles(this.props.theme);
        const {width,height,mark} = this.props;
        let color = this.props.color??d3.scaleOrdinal(["#1f77b4", "#aec7e8", "#ff7f0e", "#ffbb78", "#2ca02c", "#98df8a", "#d62728", "#ff9896", "#9467bd", "#c5b0d5", "#8c564b", "#c49c94", "#e377c2", "#f7b6d2", "#7f7f7f", "#c7c7c7", "#bcbd22", "#dbdb8d", "#17becf", "#9edae5"]);
        this.x.range([0,this.widthG()]);
        this.y.range([this.heightG(),0]);
        if(this.props.xRange)
            this.x.domain(this.props.xRange);
        if(this.props.yRange)
            this.y.domain(this.props.yRange)
        const xScale = this.state.zoomTransform.rescaleX(this.x);
        const yScale = this.state.zoomTransform.rescaleY(this.y);
        const _width = this.widthG();
        const shareColor = this.props.theme.isDark?'#696969':'black';
        return  <div style={{width: width + ' px', height: height + ' px', position: 'relative'}}>
            <svg width={width}
                 height={height}
                 viewBox={`0 0 ${width} ${height}`}
                 ref={this.svgRef}
                // style={{backgroundColor:'white'}}
                 className={styles.svg}>
                <rect className={"pantarget"} width={width} height={height} opacity="0"/>
                <g className={'content'} transform={`translate(${[this.state.margin.left+this.state.zoomTransform.x, this.state.margin.top+this.state.zoomTransform.y]})  scale(${this.state.zoomTransform.k})`}>
                    {this.props.data.map(d=><g className={"Line"} key={d.key}
                       fill={"none"} strokeOpacity={1} strokeWidth={2}>
                        <path d={''+this.LineFunc(d.value)}
                              stroke={d.key!=='_share_'?(d.color??color(d.key)):shareColor}/>
                    </g>)}
                </g>
                <g className={"axis"} transform={`translate(${[this.state.margin.left, this.state.margin.top]})`}>
                    <g className={"xaxis"} transform={`translate(${[0, this.heightG()]})`}>
                        <line x2={_width} stroke={this.props.theme.isDark?'white':'black'}/>
                        {xScale.ticks(_width>400?10:5).map((t:any)=><g className={"ticks"}>
                            <line transform={`translate(${[xScale(t),0]})`} y2={5} stroke={this.props.theme.isDark?'white':'black'}/>
                            <text x={xScale(t)} textAnchor={"middle"} y={5} dy={'1rem'} >{multiFormat(t)}</text>
                        </g>)}
                        <text y={5} dy={'2.5rem'} x={this.widthG()/2} textAnchor={"middle"} style={{fontWeight:'bold'}}>Time</text>
                    </g>
                    <g className={"yaxis"} transform={`translate(${[-8,0]})`}>
                        <g ><Legend width={8} height={this.heightG()} value={'Greys'} domain={[0,0.8]}/></g>
                        <line x1={8} x2={8} y2={this.heightG()} stroke={this.props.theme.isDark?'white':'black'}/>
                        {yScale.ticks(5).map((t:any)=><g className={"ticks"}>
                            <line transform={`translate(${[0,yScale(t)]})`} x2={-5} stroke={this.props.theme.isDark?'white':'black'}/>
                            <text y={yScale(t)} textAnchor={"end"} x={-7} dy={6} >{t}</text>
                        </g>)}
                    </g>
                    {mark? <line transform={`translate(${xScale(mark)},0)`} y2={this.heightG()} strokeDasharray={'2 1'} stroke={this.props.theme.isDark?'white':'black'}/>:''}
                </g>
            </svg>
        </div>
    }
}

const getStyles = stylesFactory((theme:GrafanaTheme2) => {

    return {svg:css`
            & text
            {fill: ${theme.isDark?'white':'black'}}
            & .fade 
            {opacity: 0.2;}
            & .onhighlight .outer_node:not(.highlight) 
                  {opacity: 0.1;}
            & .onhighlight .outer_node.highlight 
                  {opacity: 1;}
            `}
})
