import React from "react";
import { ScaleOrdinal, Arc } from 'd3';
import * as d3 from 'd3';
import {UserOBject} from "./Ulti";
import {GrafanaTheme2} from "@grafana/data";
import {stylesFactory} from "@grafana/ui";
import {css, cx} from "@emotion/css";

interface Props {
    width: number,
    height: number,
    data: Input[],
    color?: ScaleOrdinal<any,any>,
    maxValue:number,
    refValue?:number,
    highlight?: Record<string, any>,
    theme: GrafanaTheme2,
    onseperate: Function,
    overlay?:{key:string,value:number,parent:string}
}
interface PieData<T> {
    data: T,
    endAngle: number,
    index: number,
    padAngle: number,
    startAngle: number,
    value: number
    insideData?:{key:string, endAngle: number,
        padAngle: number,
        startAngle: number,}[]
}
interface Input {key:string,value:UserOBject,text?:string}
let emptyKey = "No User";
interface States {
    scalezoom?: number,
    margin: { top: number, bottom: number, left: number, right: number },
    dataViz: PieData<Input>[],
    realTotal: number,refTotal: number,virtualTotal: number,
    seperate?:PieData<Input>,
    overlay?:{key:string, endAngle: number,
        padAngle: number,
        color?:any,
        startAngle: number,}[]
}
export default class PieChart extends React.Component<Props, States> {
    svgRef = React.createRef<SVGSVGElement>();
    widthView() {
        return this.props.width * (this.state.scalezoom ?? 1)
    }

    heightView() {
        return this.props.height * (this.state.scalezoom ?? 1)
    }

    widthG() {
        return this.widthView() - (this.state.margin ?? {
            top: 0,
            bottom: 0,
            left: 0,
            right: 0
        }).left - (this.state.margin ?? {top: 0, bottom: 0, left: 0, right: 0}).right
    }

    heightG() {
        return this.heightView() - (this.state.margin ?? {
            top: 0,
            bottom: 0,
            left: 0,
            right: 0
        }).top - (this.state.margin ?? {top: 0, bottom: 0, left: 0, right: 0}).bottom
    }
    centerX () {
        return this.state.margin.left+this.widthG()/2;
    }
    centerY () {
        return this.state.margin.top+this.heightG()/2;
    }
    handleData(data:Input[]):NonNullable<States>{
        data.sort((a,b)=>b.value.totalCore-a.value.totalCore);
        const realTotal =  d3.sum(data,d=>d.value.totalCore);
        const refTotal =  this.props.refValue??realTotal; // physic core
        const virtualTotal = realTotal-refTotal;
        data.forEach(d=>{
            d.value.value=(d.value.totalCore/realTotal)* refTotal;
            if((d.value.totalCore_share!==undefined) && (d.value.totalCore_notShare!==undefined)){
                const _sum = d.value.totalCore_share+d.value.totalCore_notShare;
                d.value.inside = [(d.value.totalCore_notShare/_sum),(d.value.totalCore_share/_sum)];
            }
        })
        data.push({
            key: emptyKey,
            value:{value: this.props.maxValue - refTotal}
        });
        const dataViz = d3.pie<Input>().sort(null).value(d=>d.value.value)(data);
        dataViz.forEach((seperate )=>{
            if(seperate.data.value.inside){
                const end = (seperate.endAngle-seperate.startAngle)*seperate.data.value.inside[0]+seperate.startAngle;
                (seperate as PieData<Input>).insideData=[{key:seperate.data.text as string,endAngle:end,padAngle:0,startAngle:seperate.startAngle},
                    {key:'_share_',endAngle:seperate.endAngle,padAngle:0,startAngle:end}].filter((d,i)=>seperate.data.value.inside[i]);
            }
        })
        return {dataViz,realTotal,refTotal,virtualTotal} as NonNullable<States>;
    }
    state:States ={
        margin: { top: 0, bottom: 0, left: 50, right: 50    },
        dataViz: [],
        realTotal:0,refTotal:0,virtualTotal:0,
        seperate:undefined
    }
    componentDidMount(): void {
        this.setState(this.handleData(this.props.data.slice()) as NonNullable<States>)
    }

    componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<States>, snapshot?: any): void {
        let opt: Partial<States> = {}
        if(this.props.data!==prevProps.data || this.props.refValue!==prevProps.refValue ||this.props.maxValue!==prevProps.maxValue)
        {
            opt = {...this.handleData(this.props.data.slice())};
            opt.seperate = undefined;
            this.setState(opt as NonNullable<States>);
            this.updateOverlay(opt);
        }else
        if (this.props.overlay !== prevProps.overlay) {
            this.updateOverlay();
        }

    }
    updateOverlay(opt:Partial<States> = {}){
        if(this.props.overlay) {
            const dataViz = opt.dataViz??this.state.dataViz;
            if (dataViz.length) {
                const parent = dataViz.find(d => d.data.key === this.props.overlay?.parent);
                if(parent){
                    const ratio = this.props.overlay?.value/(parent.data.value.totalCore_share+parent.data.value.totalCore_notShare);
                    const endAngle = parent.startAngle+(parent.endAngle-parent.startAngle)*ratio;
                    this.setState({overlay:[{key:this.props.overlay?.key, endAngle,
                            padAngle: parent.padAngle,
                            color:'#ddd',
                            startAngle: parent.startAngle,}]} as NonNullable<States>)
                }else{
                    this.setState({overlay:undefined} as NonNullable<States>)
                }
            }
        }
        else{
            this.setState({overlay:undefined} as NonNullable<States>)
        }
    }
    onClick(d:PieData<Input>){
        if(this.state.seperate){ // not seperated
            this.props.onseperate(d.data.key,false);
            this.setState({seperate:undefined})
        }else{// seperated
            this.props.onseperate(d.data.key,true);
            this.setState({seperate:d})
        }
    }
    render(){
        let color = this.props.color??d3.scaleOrdinal(["#1f77b4", "#aec7e8", "#ff7f0e", "#ffbb78", "#2ca02c", "#98df8a", "#d62728", "#ff9896", "#9467bd", "#c5b0d5", "#8c564b", "#c49c94", "#e377c2", "#f7b6d2", "#7f7f7f", "#c7c7c7", "#bcbd22", "#dbdb8d", "#17becf", "#9edae5"]);
        const {width,height,maxValue,highlight} = this.props;
        const {dataViz,refTotal,seperate,overlay} = this.state;
        const styles = getStyles(this.props.theme);
        let radius = Math.min(this.widthG(),this.heightG())/2;
        const arc = d3.arc<any>()
            .outerRadius(radius * 0.8)
            .innerRadius(radius * 0.4);

        const outerArc = d3.arc()
            .innerRadius(radius * 0.9)
            .outerRadius(radius * 0.9);

        const sumlabel = dataViz[dataViz.length-1];
        const sumValue = (refTotal/maxValue)*100;
        const highlightl = highlight?Object.keys(highlight.node).length:0;
        const colorShare = this.props.theme.isDark?'#696969':'black';
        return <svg width={width}
                    height={height}
                    viewBox={`0 0 ${width} ${height}`}
                    // style={{background:'white'}}
                    ref={this.svgRef}>
            {highlight?.time?<text x={this.centerX()} y={18} textAnchor={'middle'} fill={this.props.theme.isDark?'white':'black'} >{new Date(+highlight.time).toLocaleString()}</text>:''}
                <g className={highlight?cx('content',styles.onHighlight):styles.content} transform={`translate(${this.centerX()},${this.centerY()})`}>
                    <g className={'slices'}>
                                {dataViz.map(d=><g className={'slice'+((highlight && highlight.node[d.data.key])?(' highlight'+(highlightl===1?' clickable':'')):'')+((seperate&&seperate.insideData)?' seperated':'')} key={d.data.key}
                                                   onClick={()=>this.onClick(d)}>
                            <path className={'slice'+(d.data.key===emptyKey?' hide':'')} opacity={0.7} fill={color(d.data.key)} d={arc(d)??''}/>
                            <title>{d.data.key+' #Cores: '+d.value}</title>
                            <text dy={'.35em'} key={d.data.key}
                                  transform={`translate(${getPosText(d,outerArc,radius)})`}
                                  textAnchor={midAngle(d) < Math.PI ? "start":"end"}
                                // fill={this.props.theme.isDark?'white':'black'}
                                  fill={color(d.data.key)}
                                  className={'textLabel'+((d.data.key===emptyKey)?' hide':(((d.endAngle-d.startAngle)<Math.PI/10)?' hide':'' ))}>
                                {d.data.text}
                            </text>
                            <polyline key={d.data.key}
                                      stroke={this.props.theme.isDark?'white':'black'}
                                      points={''+getPointLine(d,outerArc,arc,radius)}
                                      className={highlight?(highlight.node[d.data.key]?'':'hide'):(((d.data.key===emptyKey)||(d.endAngle-d.startAngle)<Math.PI/10)?' hide':'')}/>
                        </g>)}
                    </g>
                    {(seperate&&seperate.insideData)?<g style={{pointerEvents:'none'}}>{
                        seperate.insideData.map(d=><g fill={(d.key=='_share_')?colorShare:color(d.key)}>
                            <path className={'slice'} opacity={1} d={''+arc(d)}/>
                            <text dy={'.35em'}
                                  transform={`translate(${getPosText(d,outerArc,radius)})`}
                                  textAnchor={midAngle(d) < Math.PI ? "start":"end"}
                                  className={'textLabel'}>
                                {d.key==='_share_'?'Share':d.key}
                            </text>
                            <polyline
                                fill={'none'}
                              stroke={this.props.theme.isDark?'white':'black'}
                              points={''+getPointLine(d,outerArc,arc,radius)}/>
                        </g>)
                    }</g>:''}
                    {(overlay&&overlay)?<g style={{pointerEvents:'none'}}>{
                        overlay.map(d=><g fill={d.color}>
                            <path className={'slice'} opacity={1} d={''+arc(d)}/>
                            <text dy={'.35em'}
                                  transform={`translate(${getPosText(d,outerArc,radius)})`}
                                  textAnchor={midAngle(d) < Math.PI ? "start":"end"}
                                  className={'textLabel'}>
                                {d.key}
                            </text>
                            <polyline
                                fill={'none'}
                                stroke={this.props.theme.isDark?'white':'black'}
                                points={''+getPointLine(d,outerArc,arc,radius)}/>
                        </g>)
                    }</g>:''}
                    {/*<g className={'labels'}>*/}
                    {/*    {dataViz.map(d=><text dy={'.35em'} key={d.data.key}*/}
                    {/*                          transform={`translate(${getPosText(d,outerArc,radius)})`}*/}
                    {/*                          textAnchor={midAngle(d) < Math.PI ? "start":"end"}*/}
                    {/*                          // fill={this.props.theme.isDark?'white':'black'}*/}
                    {/*                          fill={color(d.data.key)}*/}
                    {/*                          className={highlight?(highlight.node[d.data.key]?'':'hide'):(((d.data.key===emptyKey)||(d.endAngle-d.startAngle)<Math.PI/10)?' hide':'')}>*/}
                    {/*        {d.data.text}*/}
                    {/*    </text>)*/}
                    {/*    }*/}
                    {/*</g>*/}
                    {/*<g className={'lines'} opacity={0.5} fill={'none'}>*/}
                    {/*    {dataViz.map(d=><polyline key={d.data.key}*/}
                    {/*                              stroke={this.props.theme.isDark?'white':'black'}*/}
                    {/*                              points={''+getPointLine(d,outerArc,arc,radius)}*/}
                    {/*                              className={highlight?(highlight.node[d.data.key]?'':'hide'):(((d.data.key===emptyKey)||(d.endAngle-d.startAngle)<Math.PI/10)?' hide':'')}/>)}*/}
                    {/*</g>*/}
                    {sumlabel?<><text className={'sumLabels'} textAnchor={'middle'}
                                    fill={this.props.theme.isDark?'white':'black'}
                                    style={{fontWeight:'bold', fontSize:'16px'}}>
                        {/*{d3.format('.2f')(100-((sumlabel.endAngle-sumlabel.startAngle)/Math.PI/2)*100)+'%'}*/}
                        {d3.format('.2f')(sumValue)+'%'}
                    </text>
                    </>:''}
                </g>
            </svg>;
    }
}
const getStyles = stylesFactory((theme:GrafanaTheme2) => {

    return {content:css`
        & .slice
        {pointer-events: none}
    `
        ,onHighlight:css`
     & g.slice:not(.highlight)
     {
          pointer-events: none;
          & path.slice
         {opacity: 0.2;}
         & .textLabel
         {display: none;}
         & polyline
         {display: none;}
     }
    & g.slice.highlight
     {
          pointer-events: none;
          &.clickable
          {pointer-events: all;}
          & path.slice
         {opacity: 1;}
         & .textLabel
         {display: block;}
         & polyline
         {display: block;}
         &.seperated
         {
              & path.slice
             {opacity: 0;}
             & .textLabel
             {display: none;}
             & polyline
             {display: none;}
         }
     }
    `}
})
function getPosText(d:any,outerArc:Arc<any,any>,radius:number){
    const pos = outerArc.centroid(d);
    // pos[0] = radius * (midAngle(d) < Math.PI ? 1 : -1)
    return pos
}
function getPointLine(d:any,arc:Arc<any,any>,outerArc:Arc<any,any>,radius:number){
    const pos = outerArc.centroid(d);
    pos[0] = radius * 0.95 * (midAngle(d) < Math.PI ? 1 : -1);
    return [arc.centroid(d), outerArc.centroid(d)];
    // return [arc.centroid(d), outerArc.centroid(d)];
}
function midAngle(d:any){
    return d.startAngle + (d.endAngle - d.startAngle)/2;
}
